import { DataGrid, GridToolbar } from '@mui/x-data-grid';
import React, { useState } from 'react';
import { AiFillCaretDown } from "react-icons/ai";

function PricingComp(props) {
    const [page, setPage] = useState(0);
    const [pageSize, setPageSize] = useState(10);

    return (
        <>
            <div className = 'card h-100'>
                <div className = "card-header">
                    <AiFillCaretDown style = {{ cursor : "pointer" }} data-bs-toggle = "collapse" data-bs-target = {"#" + props.cardKey} aria-expanded = "false" aria-controls = {props.cardKey}/>
                    { props.pricingName } 
                </div>
            
                <div className = "collapse" id = {props.cardKey}>
                    <div className = 'card-body'>
                        <div className = 'row' style = {{ minHeight : "100px" }}>
                            <h6 className = "card-subtitle text-muted">{ props.pricingExplain["explain1"] }</h6>
                            <br/>
                            <h6 className = "card-subtitle text-muted">{ props.pricingExplain["explain2"] }</h6>
                        </div>

                        <div className = "alert alert-warning text-center" role = "alert">
                            {props.pricingExplain["explain3"] + props.price_result[0]}
                        </div>

                        <div className = "alert alert-success text-center" role = "alert">
                            {props.pricingExplain["explain4"] + props.price_result[1]}
                        </div>

                        <div className = "alert alert-danger text-center" role = "alert">
                            {props.pricingExplain["explain5"] + props.price_result[2]}
                        </div>
                        
                        { !props.val_lose && props.price_result[3] === 0 && <p className = 'text-center'></p> }
                        { !props.val_lose && props.price_result[3] !== 0 && props.price_result[3] <= props.price_result[0] && <div className = 'card-text text-center'>: ({props.price_result[3]}) &#60; ({props.price_result[0]})</div> }
                        { !props.val_lose && props.price_result[3] !== 0 && props.price_result[3] > props.price_result[0] && props.price_result[3] <= props.price_result[1] && <div className = 'card-text text-center'>: ({props.price_result[1]}) &#62; ({props.price_result[3]}) &#62; ({props.price_result[0]})</div> }
                        { !props.val_lose && props.price_result[3] !== 0 && props.price_result[3] > props.price_result[1] && props.price_result[3] <= props.price_result[2] && <div className = 'card-text text-center'>: ({props.price_result[2]}) &#62; ({props.price_result[3]}) &#62; ({props.price_result[1]})</div> }
                        { !props.val_lose && props.price_result[3] !== 0 && props.price_result[3] > props.price_result[2] && <div className = 'card-text text-center'>: ({props.price_result[3]}) &#62; ({props.price_result[2]})</div> }
                        { props.val_lose ? <p className = 'text-center' style = {{ color : "red" }}></p> : <></> }

                        { props.data.length === 0 ? <div className = 'text-center'></div> : <div className = 'row mx-auto py-2' style = {{height : "450px"}}>
                            <DataGrid
                                sx = {{
                                    "& .label" : {
                                        backgroundColor: '#b9d5ff91'
                                    }
                                }}
                                
                                getCellClassName = {(params) => {
                                    if(props.label.includes(params.field)){
                                        return 'label'
                                    }else{
                                        return ''
                                    }
                                }}
                                
                                columns = { props.columns }
                                rows = { props.data }
                                page = { page }
                                onPageChange = {(newPage) => setPage(newPage)}
                                pageSize = { pageSize }
                                onPageSizeChange={ (newPageSize) => setPageSize(newPageSize) }
                                rowsPerPageOptions = {[5, 10, 20]}
                                getRowId = { row => row.ID }
                                components = {{ Toolbar: GridToolbar }}
                                componentsProps = {{ toolbar: { showQuickFilter: true },}}
                                pagination
                                disableColumnMenu
                                disableColumnSelector
                                disableDensitySelector
                                disableColumnFilter
                                disableSelectionOnClick = { true }
                            />
                        </div>}
                        
                    </div>
                </div>
            </div>
        </>
    );
}

export default PricingComp;