export function formatExplain(ma, maType) {
    return(
        {
            "method1" : {
                "explain0": "",
                "explain1" : ` = 95% * ${ma} ${maType} + ${ma} ${maType}`,
                "explain2" : ` = 5% * ${ma} ${maType} + ${ma} ${maType}`
            },
            "method2" : {
                "explain0": "",
                "explain1" : ` = ${ma} ${maType} - ${ma} ${maType} * ( - 2)`,
                "explain2": ""
            },
            "method3" : {
                "explain0": "",
                "explain1" : ` = 1%(5%) * ${ma} ${maType} + ${ma} ${maType}`,
                "explain2": "220"
            },
        }
    )
}

