export function pricing1(year){

    return(
        {
            "explain1" : "",
            "explain2" : ` =  ${year}  x `,
            "explain3" : ` =  ${year}  x 15 = `,
            "explain4" : ` =  ${year}  x 20 = `,
            "explain5" : ` =  ${year}  x 30 = `,
        }
    );
}

export function pricing2(year){
    return({
        "explain1" : "",
        "explain2" : ` =  ${year} `,
        "explain3" : ` =  ${year}  = `,
        "explain4" : ` =  ${year}  = `,
        "explain5" : ` =  ${year}  = `,
    });
}

export function pricing3(year){
    return({
        "explain1" : "EPSEPSPER",
        "explain2" : ` = ((EPS +  ${year} EPS) / 2) *  ${year} PER`,
        "explain3" : ` = ((EPS +  ${year} EPS) / 2) *  ${year} PER = `,
        "explain4" : ` = ((EPS +  ${year} EPS) / 2) *  ${year} PER = `,
        "explain5" : ` = ((EPS +  ${year} EPS) / 2) *  ${year} PER = `,
    });
}

export function pricing4(year){
    return({
        "explain1" : "PBRBPS",
        "explain2" : ` =  ${year} PBR * `,
        "explain3" : ` =  ${year} PBR *  = `,
        "explain4" : ` =  ${year} PBR *  = `,
        "explain5" : ` =  ${year} PBR *  = `,
    });
}
